.App {
  text-align: center;
  font-family: "Chivo", sans-serif;
  width: 100%;
  height: 100%;
}
::-webkit-scrollbar {
  width: 7.5px;
}
::-webkit-scrollbar-track {
  background-color: none;
}
::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 20px gray;
  border-radius: 5px;
}

.Light {
  background-color: #f3f3f3;
  color: #282c34;
  transition: 0.1s;
  animation: fade 1s ease-in;
}

.Dark {
  background-color: #282c34;
  color: #f3f3f3;
  transition: 0.1s;
  animation: fade 1s ease-in;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
}
